.footer {
  @include padding(80px 0 55px);

  position: relative;
  overflow: hidden;

  &__bg {
    @include absolute-cover;
    @include bg-cover;

    z-index: 5;
    pointer-events: none;
  }

  &__text-content {
    @include padding-bottom(80px);

    position: relative;
    z-index: 10;
    color: $white;
    text-align: center;
  }

  &__content {
    position: relative;
    z-index: 10;
    padding: 32px 16px;
    background-color: $white;
    border-radius: $border-radius-xxl;

    @include media-breakpoint-up(xs) {
      @include padding(60px 40px);
    }
  }

  &__logo {
    display: inline-flex;
    max-width: 200px;

    @include media-breakpoint-up(xxl) {
      max-width: 260px;
    }
  }
}
