.of-contain {
  @include absolute-cover;

  object-fit: contain;
}

.of-cover {
  @include absolute-cover;

  object-fit: cover;
}

.ofp-bottom {
  offset-position: bottom;
}

%custom-scrollbar {
  @include custom-scrollbar(8px, $secondary, $primary, 5px);
}

.custom-scrollbar {
  @extend %custom-scrollbar;
}

.resize-animation-stopper * {
  transition: none !important;
}

.transition-color {
  transition: color 0.3s;
}

.bg-white-faded {
  background-color: rgba($white, 0.15);
}

.bg-shadow-skew {
  &:after {
    position: absolute;
    bottom: 0;
    left: 50%;
    display: block;
    width: calc(50% + 8px);
    height: 40px;
    background-color: rgba($dark, 0.4);
    border-radius: 8px;
    box-shadow: 5px -5px 11px rgba($dark, 0.5);
    transform: skewX(-50deg);
    content: "";
  }
}
