.header {
  &.fixed-top {
    top: 16px;
    transition: top 0.3s;

    @include media-breakpoint-up(md) {
      top: 24px;
    }

    &.active {
      top: 6px;
    }

    + .main {
      .section {
        &:first-child {
          padding-top: 120px;

          @include media-breakpoint-up(xl) {
            padding-top: 150px;
          }
        }
      }
    }
  }

  &__content {
    padding: 8px 16px;
    background-color: $white;
    border-radius: $border-radius;
    box-shadow: 0 4px 40px 0 rgba($purple-dark, 0.1);

    @include media-breakpoint-up(lg) {
      padding: 4px 4px 4px 32px;
    }
  }

  &__logo {
    display: inline-flex;
    max-width: 130px;
  }
}
