.circle-progress {
  @include size(18px);

  position: relative;
  display: inline-flex;
  background: conic-gradient($primary var(--progress), $gray-200 0deg);
  border-radius: 50%;

  &:after {
    position: absolute;
    inset: 2px;
    background-color: $white;
    border-radius: 50%;
    content: "";
  }
}
