.nav {
  &-pills {
    display: inline-flex;
    justify-content: center;
    padding: 4px;
    background-color: $white;
    border-radius: $nav-pills-border-radius;

    .nav-link {
      min-width: 120px;
      padding: 0.8em;
      font-size: 16px;
      line-height: 1.2;

      @include media-breakpoint-up(lg) {
        min-width: 146px;
        font-size: 20px;
      }

      &.active {
        font-weight: 700;
      }

      &:not(.active) {
        color: $dark;

        &:hover {
          background-color: $gray-light;
        }
      }
    }
  }

  &-solutions {
    @include list-reset;

    &__item {
      &:not(:last-child) {
        margin-bottom: 32px;
      }
    }

    &__btn {
      @include btn-reset;

      display: inline-flex;
      text-align: left;

      &.active {
        .nav-solutions__progress-pill,
        .nav-solutions__title {
          color: $primary;
        }

        .nav-solutions__progress {
          background: conic-gradient($primary var(--progress), rgba($gray-200, 0) 0deg);
        }
      }

      &:not(.active) {
        .nav-solutions__progress {
          --progress: 0deg !important;
        }

        &:hover {
          .nav-solutions__title,
          .nav-solutions__text {
            color: $dark;
          }
        }
      }
    }

    &__progress-pill {
      display: inline-flex;
      gap: 8px;
      align-items: center;
      min-height: 48px;
      padding: 12px;
      color: $secondary;
      font-size: 24px;
      background-color: $white;
      border-radius: 50rem;
      box-shadow: $box-shadow;
      transition: color 0.3s;
    }

    &__title,
    &__text {
      display: block;
      color: $secondary;
      transition: color 0.3s;
    }
  }
}
