.collapse-item {
  &__header {
    position: relative;
    padding: 32px 44px 32px 0;

    @include media-breakpoint-up(xs) {
      padding-right: 60px;
    }

    @include media-breakpoint-up(lg) {
      padding: 40px 112px 40px 32px;
    }
  }

  &__btn {
    @include size(36px);
    @include inline-flex-center;

    position: absolute;
    top: 50%;
    right: 0;
    display: block;
    padding: 0;
    color: $primary;
    font-size: 24px;
    line-height: 1.5;
    background-color: transparent;
    border: 2px solid $primary;
    border-radius: 50rem;
    transform: translate(0, -50%);
    transition:
      transform 0.3s,
      background-color 0.3s,
      color 0.3s;
    content: "";

    @include media-breakpoint-up(xs) {
      @include size(48px);

      line-height: 2;
    }

    @include media-breakpoint-up(lg) {
      right: 32px;
    }

    &:hover {
      color: $white;
      background-color: $primary;
    }

    &[aria-expanded="true"] {
      transform: translate(0, -50%) rotate(180deg);
    }
  }

  &__content {
    padding-bottom: 50px;
  }

  &__img {
    @include responsive-ratio(561, 235);

    position: relative;
    overflow: hidden;
    border-radius: $border-radius-xxl;

    @include media-breakpoint-up(lg) {
      flex-shrink: 0;
      width: 561px;
    }

    &:after {
      @include absolute-cover;

      display: block;
      background: radial-gradient(50.57% 44.53% at 82.79% 28.91%, rgb(77 121 253 / 0) 0%, rgb(65 65 236 / 0.34) 100%);
      content: "";
    }
  }

  &__border {
    border-bottom: 1px solid $gray-200;
  }
}
