.section {
  @include padding(50px 0);

  &.with-sky-decoration {
    @include padding(250px 0);
    @include rfs(100% 240px, background-size);

    color: $white;
    background-color: $blue-dark;
    background-image: url("../img/sky-bg-top.webp"), url("../img/sky-bg-bottom.webp");
    background-repeat: no-repeat;
    background-position:
      top center,
      bottom center;

    + .section {
      @include padding-top(100px);
    }
  }

  &-welcome {
    position: relative;
    display: flex;
    align-items: flex-end;
    min-height: 600px;
    color: $gray-light;
    background-color: $dark;

    @include media-breakpoint-up(xl) {
      min-height: 700px;
    }

    &__slider {
      &:after {
        @include absolute-cover;

        z-index: 5;
        background-color: rgba($black, 0.3);
        content: "";

        @include media-breakpoint-up(md) {
          content: none;
        }
      }
    }
  }
}
