.card {
  &-question-block {
    @include media-breakpoint-up(xxl) {
      display: flex;
      justify-content: center;
    }

    &__list {
      @include media-breakpoint-up(xxl) {
        flex-shrink: 0;
        width: 1566px;
      }
    }
  }

  &-question {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 24px 12px;
    text-align: center;
    background-color: $white;
    border-radius: $border-radius;
    box-shadow: $box-shadow;
    animation: card-question 7s infinite backwards;
    animation-delay: var(--animation-delay);

    @include media-breakpoint-up(lg) {
      padding: 32px 20px;
    }

    &__badge {
      display: inline-flex;
      margin-bottom: 32px;
      padding: 0.66666666em 1.33333333em;
      font-size: 24px;
      border-radius: 50rem;
      transform: translateY(20px);
      opacity: 0;
      filter: blur(10px);
      animation: card-question-badge 7s infinite backwards;
      animation-delay: var(--animation-delay);

      &.blue {
        color: $primary;
        background-color: rgba($blue, 0.2);
      }

      &.yellow {
        color: $orange;
        background-color: rgba(#ffa800, 0.14);
      }

      &.red {
        color: $red;
        background-color: rgba(#ff0000, 0.14);
      }

      &.green {
        color: $green;
        background-color: rgba(#0dff00, 0.14);
      }

      &.purple {
        color: $indigo;
        background-color: rgba(#aa00ff, 0.14);
      }
    }

    &__body {
      display: flex;
      flex-direction: column;
      flex-grow: 1;
      justify-content: center;
      min-height: 64px;

      @include media-breakpoint-up(xs) {
        min-height: 84px;
      }

      @include media-breakpoint-up(lg) {
        min-height: 96px;
      }
    }

    &__text {
      margin-bottom: 0;
      transform: translateY(10px);
      opacity: 0;
      animation: card-question-text 7s infinite backwards;
      animation-delay: calc(var(--animation-delay) + 0.1s);

      @include media-breakpoint-down(lg) {
        font-size: 14px;
      }
    }
  }

  &-feature {
    width: 100%;
    padding: 20px 20px 44px;
    background-color: $white;
    border-radius: $border-radius-lg;
    box-shadow: $box-shadow;

    &__icon {
      @include size(64px);
      @include inline-flex-center;

      margin-bottom: 20px;
    }

    &__icon-holder {
      @include size(32px);

      position: relative;
      overflow: hidden;
    }
  }

  &-advantage-block {
    &:hover {
      .card-advantage {
        &:not(:hover) {
          @include media-breakpoint-up(md) {
            transform: scale(0.9);
            opacity: 0.75;
            filter: blur(2px);
          }
        }
      }
    }
  }

  &-advantage {
    width: 100%;
    padding: 32px 16px;
    overflow: hidden;
    background-color: $white;
    border-radius: $border-radius-xxl;
    box-shadow: $box-shadow;

    @include media-breakpoint-up(md) {
      transition:
        box-shadow 0.3s,
        transform 0.3s,
        opacity 0.3s,
        filter 0.3s;
    }

    &:hover {
      @include media-breakpoint-up(md) {
        box-shadow: $box-shadow-lg;
        transform: scale(1.025);
      }
    }

    &__content {
      position: relative;
      z-index: 10;
    }

    &__text-with-side-line {
      padding-left: 8px;
      color: $tertiary;
      border-left: 1px solid $primary;
    }

    &.version-1 {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding-bottom: 0;

      @include media-breakpoint-up(md) {
        padding: 26px 40px 0;
      }

      .card-advantage__img {
        @include responsive-ratio(16, 9);

        position: relative;
        z-index: 5;
        width: 100%;
        max-width: 500px;
        margin: 0 auto;
        overflow: hidden;

        @include media-breakpoint-up(xl) {
          max-width: 100%;
          margin-top: -50px;
        }

        img {
          object-position: bottom;
        }
      }
    }

    &.version-2 {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding-bottom: 0;

      @include media-breakpoint-up(md) {
        padding: 35px 12px 0;
      }

      .card-advantage__img {
        @include responsive-ratio(16, 9);

        position: relative;
        z-index: 5;
        margin-right: -16px;
        margin-left: -16px;
        overflow: hidden;

        @include media-breakpoint-up(md) {
          margin-right: -12px;
          margin-left: -12px;
        }

        img {
          object-position: bottom;
        }
      }
    }

    &.version-3 {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding-top: 0;

      @include media-breakpoint-up(md) {
        padding: 0 16px 32px;
      }

      .card-advantage__content {
        margin-top: -60px;
      }

      .card-advantage__img {
        @include responsive-ratio(16, 9);

        position: relative;
        z-index: 5;
        margin-right: -16px;
        margin-left: -16px;
        overflow: hidden;

        img {
          object-position: top;
        }
      }
    }

    &.version-4 {
      display: flex;
      flex-direction: column;
      padding-bottom: 0;

      @include media-breakpoint-up(md) {
        padding: 44px 18px 0;
      }

      .card-advantage__img {
        position: relative;
        flex-grow: 1;
        min-height: 300px;
        overflow: hidden;

        img {
          object-position: bottom;

          @include media-breakpoint-up(md) {
            object-position: bottom right;
          }
        }
      }
    }
  }
}
