.link {
  &-secondary {
    transition: color 0.3s;

    &:hover {
      color: $primary !important;
    }
  }

  &-tertiary {
    transition: color 0.3s;

    &:hover {
      color: $primary !important;
    }
  }

  &-dark {
    transition: color 0.3s;

    &:hover {
      color: $primary !important;
    }
  }

  &-with-underline-animation {
    position: relative;
    display: inline-block;

    &:after {
      position: absolute;
      bottom: -2px;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: $primary;
      border-radius: 2px;
      transform: scaleX(0);
      transform-origin: right center;
      transition: transform 0.5s cubic-bezier(0.694, 0, 0.335, 1);
      content: "";
    }

    &:hover {
      &:after {
        transform: scaleX(1);
        transform-origin: left center;
      }
    }
  }

  &-social {
    display: inline-flex;
    font-size: 40px;
  }
}

.links-with-divider {
  [class*="col-"] {
    position: relative;

    &:not(:last-child) {
      &:after {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        display: block;
        width: 1px;
        height: 100%;
        background-color: $gray-600;
        content: "";
      }
    }
  }
}
