.btn {
  @include inline-flex-center;

  gap: 8px;

  // sizes
  &:not(.btn-sm, .btn-lg) {
    min-width: 200px;
    min-height: 3.11111111em;

    @include media-breakpoint-down(xxl) {
      font-size: 16px;
    }

    @include media-breakpoint-down(xl) {
      font-size: 14px;
    }
  }

  [class*="icomoon-"] {
    font-weight: 400;
    font-size: 1.33333333em;
  }

  // colors
  &-primary {
    color: $white !important;
  }

  &-white {
    @include button-variant($white, $white, $blue-dark, darken($white, 7.5%), darken($white, 7.5%), $blue-dark, darken($white, 7.5%), darken($white, 7.5%), $blue-dark);
  }

  &-outline-primary {
    &:hover {
      color: $white !important;
    }
  }
}
